* {
    margin: 0;
    padding: 0;
    font-family: segoe ui, sans-serif;
}

.Transaction {
    display: flex;
    padding: 10px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.Transaction .order {
    padding-right: 10px;
}

.Transaction .username, .Transaction .bookTitle, .Transaction .transaction-state {
    flex-grow: 1;
    text-align: left;
}

.Transaction .username {
    font-weight: 500;
}

.Transaction .transaction-action button {
    padding: 5px 10px;
}