* {
    margin: 0;
    padding: 0;
    font-family: segoe ui, sans-serif;
}

.User {
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding: 10px;
}

.User .order {
    padding-right: 10px;
}

.User .username {
    flex-grow: 2;
    text-align: left;
    font-weight: 500;
}

.User .user-action button {
    padding: 5px 10px;
}